
import styled from "styled-components";
import Nav from "./Nav"
const Wrapper = styled.div`
  height: 100vh;
  display: flex;
  flex-direction: column;
`;
const Contents = styled.div`
  flex-grow: 1;
`;

const Layouts = (props:any) =>{
  return (<Wrapper>
        <Contents className={props.className}>
            {props.children}
        </Contents>
        <Nav />
    </Wrapper>);
}

export default Layouts