import { Input } from "components/Input";
import { ChangeEventHandler, useRef } from "react";
import styled from "styled-components";

const Wrapper = styled.section`
  background: #f5f5f5;
  padding: 14px 16px;
  font-size: 14px;
  label {
    display: flex;
    align-items: center;
    span {
      margin-right: 16px;
      white-space: nowrap;
    }
    input {
      display: block;
      width: 100%;
      height: 72px;
      background: none;
      border: none;
    }
  }
`;
type Props = {
  value: string;
  onChange: (note: string) => void;
};
const NotesSection: React.FC<Props> = (props) => {
  const note = props.value;
  const onchange: ChangeEventHandler<HTMLInputElement> = (e) => {
    props.onChange(e.target.value);
  };
  return (
    <Wrapper>
      <Input
        type="text"
        label="备注"
        onChange={onchange}
        value={note}
        className='outLine'
        placeholder="请填写备注"
      />
    </Wrapper>
  );
};

export { NotesSection };
